.App {
  text-align: center;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

button {
  background-color: lightgray;
  padding-left: 5%;
  padding-right: 5%;
  border-radius: 25px;
  margin: 2%;
  font-size: large;
  text-align: center;
}

button:active{
  background-color: darkgray;
}

input{
  text-align: center;
}

.textInput{
  background-color: lightgray;
  padding-left: 1%;
  padding-right: 1%;
}

h3{
  display: inline;
  margin: 13%;
  padding-bottom: 10%;
  text-align: center;
  font-size: medium;
}

p{
  display: inline;
  margin: 15%;
  text-align: center;
}